.WirePages {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  height: 100vh;
}
.left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-shrink: 0;
  width: 100px;
  min-width: 50px;
  padding-top: 50px;
  background-image: linear-gradient(to bottom, black, gray);
}

#Account1Pic {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 42px;
  width: 42px;
  border-radius: 50%;
  border: none;
  box-sizing: border-box;
  background-color: transparent;
  margin-bottom: 20px;
}

.add-accounts {
  background-color: rgb(39, 38, 38);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  border: solid;
  margin-top: 20px;
  cursor: pointer;
}
.my-image {
  border-radius: 50%;
  border-top-color: chartreuse;
  border-bottom-color: chartreuse;
  height: 30px;
  width: 30px;
}
.middle {
  display: flex;
  justify-content: flex-start;
  align-content: stretch;
  flex-direction: column;
  width: 304px;
  flex-shrink: 0;
  background-image: linear-gradient(black, rgb(66, 51, 48), black);
  color: snow;
  font-weight: bold;
}

@media screen and (max-width: 900px) {
  div.middle {
    display: flex;
    justify-content: flex-start;
    align-content: stretch;
    flex-direction: column;
    width: 45px;
    overflow: hidden;
    padding-top: 47px;
    color: snow;
    font-weight: bold;
    height: 250px;
  }
  div.prefrences-menu-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
    margin-left: 10px;
    border-style: none none solid none;
    border-top-color: black;
    border-bottom-color: black;
    border-width: thin;
    flex-grow: 1;
    cursor: pointer;
  }
  div.prefrences {
    visibility: hidden;
  }
  button.back-button {
    background-image: url("https://img.icons8.com/material-outlined/48/000000/left.png");
    height: 40px;
    width: 40px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: none;
  }
  .back-button:focus {
    outline-style: none;
  }
}

@media screen and (max-height: 700px) {
  div.under-menu {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-grow: 1;
    background-color: lightgray;
  }
}
.prefrences {
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 41px;
}
.prefrences > span {
  color: snow;
  font-size: 15px;
}
.prefrences > img {
  height: 30px;
  width: 30px;
}
.prefrences-menu {
  display: flex;
  padding-left: 10px;
  justify-content: center;
  align-items: center;
}
.prefrences-menu-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 50px;
  margin-left: 10px;
  border-style: none none solid none;
  border-top-color: snow;
  border-bottom-color: snow;
  border-width: thin;
  flex-grow: 1;
  cursor: pointer;
}
.Icon {
  cursor: default;
  color: snow;
  height: 50px;
  flex-shrink: 0;
  width: 50px;
  box-sizing: border-box;
  padding-top: 12px;
  padding-left: 10px;
}

.prefrences-menu-text > span {
  font-size: large;
}
.Display1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: stretch;
  flex-grow: 1;
  background-color: lightgray;
  height: calc(100vh - 20px);
  overflow: auto; /*width:753px ;*/
}

.right-top {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 41px;
  position: sticky;
  z-index: 10;
  background-color: lightgray;
  top: 0;
  font-size: 15px;
  border-style: none none solid none;
  border-color: lightgray;
  border-width: thin;
}
.right-bottom {
  background-color: lightgray;
}
.name {
  height: 264px;
  background-color: lightgray;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
}
.name-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: stretch;
  background-color: lightgray;
  width: 408px;
}
.ps {
  background-color: lightgray;
}
.name-image {
  display: flex;
  justify-content: flex-start;
  align-items: center;
