  cursor: pointer;
}
.back-button:focus {
  outline-style: none;
}
#click {
  height: 60px;
  width: 40px;
  border: none;
  position: relative;
  background-color: white;
  background-image: url("https://img.icons8.com/windows/30/000000/long-arrow-right.png");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  opacity: 50%;
}
#click:focus {
  border: none;
  outline: none;
}
.close > img {
  position: relative;
  right: 20px;
  top: 20px;
  height: 23px;
  width: 23px;
}
.modes {
  display: flex;

  justify-content: space-around;
  align-content: center;
}
.modechoice {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
}
.modechoice > span {
  text-align: center;
  margin-top: 10px;
}
.modechoice > a {
  font-size: 25px;
  font-weight: bold;
  margin-top: 10px;
  text-align: center;
  cursor: pointer;
}

.first-page {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightgray;
  height: 100vh;
}
.modes-container {
