}
.login > p {
  color: snow;
}
.login > h1 {
  margin-top: 50px;
  font-size: 50px;
  font-family: "Courier New", Courier, monospace;
}
.userpass {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
.back-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
.back-container > img {
  height: 40px;
  width: 40px;
}
.nothing {
  flex-grow: 1;
}
.username-inputs {
  display: flex;
  background-color: snow;
  margin: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-content: stretch;
  width: 350px;
}
.inputbox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
}
#userpassword {
  height: 60px;
  width: 300;
  border: none;
  padding-left: 10px;
}
#userpassword:focus {
  outline: none;
  border: none;
}
.userinput {
  padding-left: 10px;
  width: 340px;
  height: 60px;
  border-left: none;
  border-top: none;
  border-right: none;
}
.userinput:focus {
  outline: none;
  border-left: none;
  border-top: none;
  border-right: none;
}
.inputbox > img {
  height: 40px;
  width: 30px;
  margin-top: 10px;
  position: relative;
}
.footnotes {
  background-color: rgb(184, 184, 180);
  flex-grow: 3;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
#Invalid {
  visibility: hidden;
  align-self: center;
