  font-size: 15px;
  font-weight: bold;
  padding-bottom: 10px;
}
.footnotes > a {
  align-self: center;
  font-size: 15px;
  color: black;
  text-decoration: none;
  background-color: rgb(184, 184, 180);
}
.footnotes-bottom {
  align-self: center;
