  border-radius: 50%;
}
.name-id {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  color: black;
  margin-left: 13px;
  font-size: 15px;
  border-bottom: solid rgb(56, 11, 11);
  border-width: thin;
  width: 100%;
  padding-top: 10px;
}
.list-pics {
  margin-top: 10px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
}
.name-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-left: 10px;
  align-items: center;
  font-size: 15px;
  cursor: pointer;
}
.chat-page-top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 41px;
  position: sticky;
  z-index: 10;
  top: 0;
  font-size: 15px;
  border-style: none none solid none;
  border-color: lightgray;
  border-width: thin;
  background-color: snow;
  padding-left: 10px;
  background-color: rgb(226, 226, 221);
}
.icon-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 41px;
  width: 110px;
  font-size: 15px;
  border-style: none;
  padding-right: 10px;
  background-color: rgb(226, 226, 221);
}
.chat-page {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  flex-grow: 1;
  background-color: rgb(226, 226, 221);
  width: calc(100vw - 420px);
  height: 96vh;
}
.connection-profile {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  border-bottom: solid;
  flex-grow: 1;

  background-color: rgb(226, 226, 221);
}
.connection-profile > img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  align-self: center;
  margin-top: 30px;
  margin-bottom: 30px;
}
.time-box {
  padding-left: 30px;
  padding-bottom: 10px;
  border-bottom: solid;
  height: 30px;
  background-color: rgb(226, 226, 221);
}
.speech-name {
  display: flex;
  justify-content: flex-start;
  align-content: center;
  margin-top: 10px;
  padding-left: 10px;
}
.speech-name > span {
  margin-left: 30px;
  align-self: center;
}
.speech-text > p {
  font-size: 15px;
}
.speech-text {
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding-left: 80px;
  background-color: rgb(226, 226, 221);
}
.speech-text > span {
  overflow-wrap: break-word;
  max-inline-size: 60%;
}
.delivery {
  display: flex;
  justify-content: flex-end;
  align-content: flex-start;
  width: 140px;
  font-size: 12px;
  padding-top: 5px;
}
.delivery > span {
  margin-right: 10px;
}
.new-message {
  display: flex;
  height: 50px;
  justify-content: space-between;
  align-content: center;
}
.new-message > input {
  flex-grow: 1;
  height: 41px;
  border: none;
  padding: 10px;
  padding-left: 80px;
  box-sizing: border-box;
  overflow-y: scroll;
  overflow-x: none;
  border: none;
  overflow-y: auto;
  resize: none;
}
.new-message > input:focus {
  outline: none;
}
.message-area {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* flex-grow: 1; */
  overflow-y: auto;
  background-color: rgb(226, 226, 221);
  height: 68vh;
}
.converList {
  display: flex;
  justify-content: flex-start;
  align-content: stretch;
  flex-direction: column;
  width: 304px;
  flex-shrink: 0;
  background-image: linear-gradient(black, rgb(66, 51, 48), black);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 70%;
  color: snow;
  font-weight: bold;
}
